<template>
    <v-container
            id="markdown"
            fluid
            tag="section"
    >

                <editor
                        :initialValue="userData"
                        height="600px"
                        @change="onEditorChange"
                        ref="toastuiEditor"
                />
    </v-container>
</template>

<script>
    import 'codemirror/lib/codemirror.css';
    import '@toast-ui/editor/dist/toastui-editor.css';

    import { Editor } from '@toast-ui/vue-editor';


    export default {
        name: "MarkDown",
        components: {
            editor: Editor
        },
        data: () => ({
            userData: ''
        }),
        beforeMount() {
            this.getUserData()
        },
        mounted() {
            // Dark theme
            this.$vuetify.theme.dark = false;

            this.$on('offline', () => {
                alert('You are offline! The website will not work')
            })

        },
        methods: {
            onEditorChange() {
                // implement your code
                this.userData = this.$refs.toastuiEditor.invoke('getMarkdown')
                this.getUserData()
            },
            getUserData () {
                if (this.isOnline) {
                    // make network request that returns 'userData' object

                    this.$offlineStorage.set('user', this.userData)
                } else {
                    this.userData = this.$offlineStorage.get('user')
                }
            }
        }
    }
</script>

